import React from 'react';
import { DataGrid } from '@material-ui/data-grid';
class Countries extends React.Component {
    constructor(){
        super();
        // const rows = [
        //     { id: 1, col1: 'Hello',col2: 'World'},
        //     { id: 2, col1: 'Hello',col2: 'World'},
        //     { id: 3, col1: 'Hello',col2: 'World'},
        // ]
        const columns = [
            { field: 'Country', headerName: 'Country',width:150},
            { field: 'Slug', headerName: 'Slug',width:150},
            { field: 'ISO2', headerName: 'ISO2',width:150},
        ];
        this.state={ rows:[],columns:columns};
    }
    componentDidMount(){
        this.getData();
    }
    getData=()=> {
        console.log('ok nhe');
        fetch('https://api.covid19api.com/countries')
        .then((res)=>res.json())
        .then(
            (data)=> {
                let id =1;
                const dataWithId = data.map((x)=>
                    Object.assign({},x,{id:id++})
                );
                this.setState({rows:dataWithId});
            },
            (error)=> {
                console.log('error',error);
            }
        )
    }
    render(){
        return( 
            <div style= {{ height:700, width:'100%'}}>
                <DataGrid rows = {this.state.rows} columns = {this.state.columns}/>
            </div>
        )
    }
}
export default Countries;