import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
// import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
class ComboCountries extends React.Component {
    constructor(props){
        super(props);
        const useStyles = makeStyles((theme) => ({
            formControl: {
              margin: theme.spacing(1),
              minWidth: 200,
            },
            selectEmpty: {
              marginTop: theme.spacing(2),
            },
          }));
        const columns = [
            { field: 'Country', headerName: 'Country',width:150},
            { field: 'Slug', headerName: 'Slug',width:150},
            { field: 'ISO2', headerName: 'ISO2',width:150},
        ];
        this.state={ rows:[],columns:columns,useStyles:useStyles,selectedCountry:''};
    }
    componentDidMount(){
        this.getData();
    }
    handleChange= (e)=>{
        console.log('chọn:', e.target.value);
        this.setState({selectedCountry:e.target.value})
    }
    getData=()=> {
        console.log('ok nhe');
        fetch('https://api.covid19api.com/countries')
        .then((res)=>res.json())
        .then(
            (data)=> {
                let id =1;
                const dataWithId = data.map((x)=>
                    Object.assign({},x,{id:id++})
                );
                this.setState({rows:dataWithId});
            },
            (error)=> {
                console.log('error',error);
            }
        )
    }
    render(){
        return( 
            <div>
               <FormControl className={this.state.useStyles.formControl}>
                    <InputLabel id="demo-simple-select-label" style={{color:'white'}}>Country</InputLabel>
                    <Select
                    labelId="demo-simple-select-label"
                    id="demo-simple-select"
                    style={{minWidth:200}}
                    value={this.state.selectedCountry}
                    onChange={this.handleChange}
                    >
                    {this.state.rows.map((value,index) => {
                        return (
                            <MenuItem  value={value.Country}>{value.Country}</MenuItem>
                        );
                    })}
                    </Select>
                </FormControl>
            </div>
        )
    }
}
export default ComboCountries;